.cookieknight__btn{box-sizing:border-box;padding:6px 24px;margin-right:6px;min-height:48px;display:inline-block;border:0;background-color:#ccc;font-size:16px;cursor:pointer;margin-bottom:12px}.cookieknight__btn:hover{background-color:#f2f2f2}.cookieknight__btn--primary{background-color:#293a80;color:#fff}.cookieknight__btn--primary:hover{background-color:#3c54ba}@media(max-width:800px){.cookieknight__btn{width:100%}}.cookieknight{line-height:1.5;color:#fff;box-sizing:border-box;background-color:#010038;position:fixed;bottom:0;left:0;right:0;margin-left:auto;margin-right:auto;width:100%;padding:24px 12px;font-family:Arial,sans-serif;font-size:16px;text-align:center}@media(max-width:800px){.cookieknight{height:100%}}.cookieknight__close{position:absolute;top:12px;right:12px;border:0;font-size:18px;cursor:pointer;background:transparent;color:#fff}.cookieknight__intro{width:90%;margin:0 auto 24px}.cookieknight-detail{border:0;color:#fff;line-height:1.5;box-sizing:border-box;position:absolute;min-height:70%;top:0;left:0;right:0;margin:auto;background:#010038;padding:12px 24px;font-family:Arial,sans-serif;font-size:16px;box-shadow:0 6px 6px rgba(0,0,0,.5)}@media(min-width:800px){.cookieknight-detail{position:fixed;bottom:0;max-height:90%;width:40%}}.cookieknight-detail a{color:#fff;text-decoration:underline}.cookieknight-detail__wrapper{height:100%}.cookieknight-detail__headline{font-size:32px;margin-top:0;margin-bottom:6px;width:90%}.cookieknight-detail__footer{background-color:#010038;box-sizing:border-box;margin-top:24px;text-align:center;border-top:1px solid #fff;padding-top:18px}@media(min-width:800px){.cookieknight-detail__footer{margin-left:auto;margin-right:auto;position:absolute;left:0;right:0;bottom:0}}@media(min-width:800px){.cookieknight-detail__cookies{max-height:70%;overflow-y:scroll}}.cookieknight-detail__cookies .cookieknight-detail__cookie:last-child{margin-bottom:72px}.cookieknight-detail__cookie{box-sizing:border-box;padding:12px;background-color:#537ec5;margin-top:24px;display:flex}.cookieknight-detail__cookie:not(.cookieknight-detail__cookie__checked):hover{background-color:#2d4e85}.cookieknight-detail__cookie__headline{font-size:18px;font-weight:700;margin-top:0;margin-bottom:6px;cursor:pointer}.cookieknight-detail__cookie__purpose{margin:0;font-size:13px}.cookieknight-detail__cookie__required{font-style:italic;font-size:12px;display:inline-block;margin-left:2px}.cookieknight-detail__cookie__check{margin-right:6px}.cookieknight-detail__cookie__check input:focus{outline:4px solid rgba(1,0,56,.5)}.cookieknight-detail__cookie__checked{opacity:1%;background-color:#ccc;color:#010038}